@charset "UTF-8";
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html body {
  background: #121116;
  color: #ffffff;
  font-family: franklin-gothic-compressed, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px; }
  html body .wrapper {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    column-gap: 20px;
    grid-row-gap: 0;
    grid-template-areas: "c c c c c c c c c s s s" "fl fl fl fl ft fm fm fm fm fm fs fs";
    grid-template-rows: auto 125px;
    margin: 0 30px; }
  html body ul li {
    list-style-type: none; }

header {
  height: 70px; }

#slider {
  height: 420px; }
  #slider .slide {
    height: 100%; }
  #slider .blur {
    height: 100%;
    background: url(../img/Vector.png) right no-repeat;
    background-size: contain; }

#content {
  margin: 35px 0 45px 0; }
  #content .bread {
    margin-bottom: 20px;
    font-size: 20px; }
    #content .bread hr {
      margin-top: 2px;
      border: none;
      color: red;
      background-color: red;
      height: 2px;
      width: 80px; }
  #content .wrapper #news {
    width: 100%;
    grid-area: c; }
    #content .wrapper #news .view-style {
      display: flex;
      justify-content: space-between; }
    #content .wrapper #news .feed .news-preview {
      margin-bottom: 35px; }
      #content .wrapper #news .feed .news-preview hr {
        margin: 6px 0;
        border: none;
        color: red;
        background-color: red;
        height: 2px;
        width: 80px; }
      #content .wrapper #news .feed .news-preview img {
        margin-bottom: 10px; }
      #content .wrapper #news .feed .news-preview .title {
        font-size: 20px; }
      #content .wrapper #news .feed .news-preview .date {
        font-size: 13px;
        color: #787878; }
    #content .wrapper #news .more {
      display: flex;
      justify-content: center; }
      #content .wrapper #news .more .btn-more {
        cursor: pointer;
        padding: 18px 24px;
        border: 2px solid #FFFFFF;
        border-radius: 5px;
        display: inline-block; }
        #content .wrapper #news .more .btn-more p {
          font-size: 18px; }
      #content .wrapper #news .more :hover {
        background: #25242e; }
  #content .wrapper #sidebar {
    grid-area: s; }
    #content .wrapper #sidebar hr {
      width: 70px; }
    #content .wrapper #sidebar .filter {
      margin-bottom: 40px; }
      #content .wrapper #sidebar .filter li {
        display: flex;
        justify-content: space-between;
        padding: 20px 27px;
        font-size: 18px;
        background: #25242e;
        border-bottom: 1px solid rgba(84, 84, 84, 0.45); }
        #content .wrapper #sidebar .filter li input {
          display: none;
          /* <--скрываем дефолтный чекбокс */ }
        #content .wrapper #sidebar .filter li span {
          /* <-- стилизируем новый */
          height: 15px;
          width: 15px;
          display: inline-block;
          position: relative;
          background: #DBDBDB;
          box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
          border-radius: 50%;
          padding: 3px; }
        #content .wrapper #sidebar .filter li [type=checkbox]:checked + span:before {
          /* <-- ставим иконку, когда чекбокс включен  */
          content: '•';
          position: absolute;
          top: -16px;
          left: 0;
          font-size: 43px;
          color: red; }

footer {
  height: 125px;
  background: #25242e; }
  footer a {
    color: #ffffff;
    text-decoration: none; }
  footer .wrapper div {
    justify-self: start;
    align-self: center; }
  footer .wrapper .logo {
    grid-area: fl; }
  footer .wrapper .to-top {
    grid-area: ft; }
  footer .wrapper .sitemap {
    grid-area: fm;
    display: flex; }
    footer .wrapper .sitemap ul {
      margin-right: 60px; }
      footer .wrapper .sitemap ul li {
        line-height: 1.7em; }
  footer .wrapper .social {
    grid-area: fs;
    justify-self: end; }
    footer .wrapper .social ul li {
      display: inline; }
